.SingleCause .wrapper {
  background-color: var(--white);
  border-top-left-radius: 1.5rem;
  border-top-right-radius: 1.5rem;
  margin-top: -4.5rem;
  padding-top: 4rem;
  z-index: 1;
}

.share-page {
  background-color: var(--bright-green);
  /* border: 1px solid var(--light-gray); */
  border-radius: 5px;
  box-shadow: var(--black-box-shadow);
  color: var(--white);
  margin: 1rem auto 3rem !important;
  padding: 0.75rem !important;
  width: 85% !important;
}

.share-link {
  color: var(--text-gray);
  margin: 2rem auto;
  padding: 0.5rem 0rem;
  text-align: center;
  text-decoration: underline;
  width: 50%;
}

.share-link i {
  margin-right: 0.5rem;
}

.donate-link {
  background-color: var(--dark-green);
  border-radius: var(--border-radius);
  box-shadow: var(--black-box-shadow);
  color: var(--white);
  margin-left: 5% !important;
  margin-right: auto !important;
  width: 90% !important;
}

.MainImage {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border: 2px solid var(--bright-green);
  box-shadow: 0.2rem 0.3rem 2.5rem rgba(0,0,0,0.5);
  height: 6.5rem;
  margin: -20% auto 0;
  overflow: hidden;
  position: relative;
  text-align: center;
  top: -1rem;
  width: 6.5rem;
}

.MainImage .fa-camera {
  align-self: center;
  color: var(--text-gray);
  font-size: 2.75rem;
}
.Header .hero-image {
  align-items: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: right center;
  display: flex;
  justify-content: center;
  height: 29vh;
  position: relative;
  -webkit-transform: translateY(-10%);
          transform: translateY(-10%);
  z-index: -1;
}

.Header .hero-image h2 {
  color: var(--white);
  letter-spacing: 2px;
  margin-top: 2rem;
  text-shadow: 0.2rem 0.2rem 0.5rem rgba(0,0,0,1);
}

.Header .fa-image {
  align-self: center;
  color: var(--text-gray);
  font-size: 2.75rem;
}

.ProgressBar {
  border: 1px solid var(--light-gray);
  border-radius: 5px;
  margin: 2rem auto;
  padding: 0.5rem;
  position: relative;
  width: 90%;
}

.percent-bar {
  background-color: var(--light-green);
  height: 20px;
  overflow: visible;
  padding: 0.8rem 0.5rem;
  -webkit-transition: width 0.3s;
  transition: width 0.3s;
}

.percent-bar h3 {
  color: var(--dark-green);
  letter-spacing: 0.1rem;
  position: absolute;
}

.Tile {
  border: 1px solid var(--light-gray);
  border-radius: 5px;
  height: 43px;
  margin: 0.5rem 0.3rem;
  padding: 0.3rem;
  padding-bottom: 1px;
  position: relative;
  width: 40px;
}

.Tile img {
  height: 40px;
  width: 40px;
}

.Tile .tile-amount {
  align-items: center;
  background-color: var(--dark-green-85);
  color: var(--white);
  display: flex;
  justify-content: center;
  height: 40px;
  position: absolute;
  -webkit-transition: background-color 0.3s, color 0.3s, border-radius 0.3s, -webkit-transform 0.3s;
  transition: background-color 0.3s, color 0.3s, border-radius 0.3s, -webkit-transform 0.3s;
  transition: background-color 0.3s, color 0.3s, transform 0.3s, border-radius 0.3s;
  transition: background-color 0.3s, color 0.3s, transform 0.3s, border-radius 0.3s, -webkit-transform 0.3s;
  width: 40px;
}

.tile-amount.isSelected {
  background-color: var(--light-green-85);
  border-radius: 5px;
  color: var(--blackish);
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
  -webkit-transition: background-color 0.3s, color 0.3s, border-radius 0.3s, -webkit-transform 0.3s;
  transition: background-color 0.3s, color 0.3s, border-radius 0.3s, -webkit-transform 0.3s;
  transition: background-color 0.3s, color 0.3s, transform 0.3s, border-radius 0.3s;
  transition: background-color 0.3s, color 0.3s, transform 0.3s, border-radius 0.3s, -webkit-transform 0.3s;
}

.Tile.isPurchased {
  pointer-events: none;
}

.Tile.isPurchased p {
  background-color: rgba(255,255,255,0.6);
  color: var(--blackish);
}

.TileSection {
  padding: 1rem;
  position: relative;
}

.TileSection h2 {
  color: var(--dark-green);
  text-transform: uppercase;
  text-align: center;
}

.tile-wrapper {
  display: flex;
  flex-wrap: nowrap;
  margin: 2rem 0;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: -ms-autohiding-scrollbar;
}

.tile-wrapper .row{
  flex: 0 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-right: 1.5rem;
  max-width: 100%;
}

.direction-arrows {
  color: var(--text-gray);
  position: absolute;
  top: 50%;
  width: 0%;
}

.direction-arrows.left {
  left: 1vw;
}

.direction-arrows.right {
  right: 6vw;
}

.TextSection {
  margin-bottom: 2rem;
}

.TextSection p {
  color: var(--text-gray);
  font-size: 1.2rem;
  line-height: 1.6rem;
  /* letter-spacing: 0.5px; */
  padding: 0 1rem;
}

.AboutCause {
  margin: 2rem auto 0rem;
}

.DonorComments {
  margin-bottom: 3rem;
}

.comment-card {
  align-items: center;
  border: var(--border);
  border-radius: var(--border-radius);
  display: flex;
  height: 7rem;
  justify-content: space-around;
  margin: 0.5rem auto;
  width: 92%;
}

.comment-card div {
  position: relative;
}

.comment-card img {
  border-radius: 50%;
  border: 2px solid var(--bright-green);
  width: 86px;
  height: 86px;
}

.donor-amount {
  align-items: center;
  background-color: var(--dark-green-85);
  border-radius: 50%;
  color: var(--white);
  display: flex;
  font-size: 0.6rem;
  height: 1.6rem;
  justify-content: center;
  position: absolute;
  top: 0;
  right: 0;
  width: 1.6rem;
}

.ActionButton .fas {
  margin-right: 0.5rem;
}

