.DonorComments {
  margin-bottom: 3rem;
}

.comment-card {
  align-items: center;
  border: var(--border);
  border-radius: var(--border-radius);
  display: flex;
  height: 7rem;
  justify-content: space-around;
  margin: 0.5rem auto;
  width: 92%;
}

.comment-card div {
  position: relative;
}

.comment-card img {
  border-radius: 50%;
  border: 2px solid var(--bright-green);
  width: 86px;
  height: 86px;
}

.donor-amount {
  align-items: center;
  background-color: var(--dark-green-85);
  border-radius: 50%;
  color: var(--white);
  display: flex;
  font-size: 0.6rem;
  height: 1.6rem;
  justify-content: center;
  position: absolute;
  top: 0;
  right: 0;
  width: 1.6rem;
}
