.Header .hero-image {
  align-items: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: right center;
  display: flex;
  justify-content: center;
  height: 29vh;
  position: relative;
  transform: translateY(-10%);
  z-index: -1;
}

.Header .hero-image h2 {
  color: var(--white);
  letter-spacing: 2px;
  margin-top: 2rem;
  text-shadow: 0.2rem 0.2rem 0.5rem rgba(0,0,0,1);
}

.Header .fa-image {
  align-self: center;
  color: var(--text-gray);
  font-size: 2.75rem;
}
